body {
  background-image: url("../images/starry_sky.jpg");
  background-size: 100;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Courier New";
}
.me_image {
  max-width: 350px;
  min-height: auto;
}
.img_of_the_hidden_leaf {
  max-width:200px;
  min-height: auto;
  z-index: 10;
}
.border-active {
  border: 3px dashed #cec0fc;
}
div {
  padding: 20px;
}
#quote{
  z-index: 2;
}
#me_img_div{
  z-index: 1;
}
.about-table {
  border: 3px dashed #cec0fc; /* lavender */
  border-collapse: collapse;
}
.noscript_img {
  max-width: 250px;
  min-height: auto;
}
th, td {
  padding: 15px;
}
a {
  color: black;
}
.readability {
  background:white;
  padding: 60px 100px 60px 100px;
}
.blog-image {
  max-width: 200px;
  min-height: auto;
}
.visitor-painting {
  border: 4px #ffea61;
}
.rotateme {
  width: 400px;
  display: block;
  transform: rotate(90deg);
}
